@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');
@import url(https://cdnjs.cloudflare.com/ajax/libs/weather-icons/2.0.9/css/weather-icons.min.css);

html{
    font-size: 62.25%;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

.body {
    background-color: #43AFFC;
    /* display: flex;
    justify-content: center;
    align-items: center; */
    /* background-image: url("bg1");url(`${bg1}`); */
    background-position: top center;
    background-size: cover;
    background-repeat: no-repeat;
    color: white;
    /* max-width: 100vw;
    max-height: 100vh; */
    overflow: hidden;
}

.appwrap {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
    min-height: 100vh;
    min-width: 100vw;
    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.6))
}

.header{
    margin: 10rem auto 2rem  auto;
    /* background-color: blue; */
    padding: 1rem;
    position: relative;
}

.infotext{
    font-size: 1.3rem;
    display: flex;
    align-items: center;
    /* justify-content: center; */
    margin: 0 0 1rem 0;
    padding-left: 3rem;
}

.errortext{
    opacity: 1;
}
.errortext2{
    opacity: 1;
}

.search{
    /* background-color: red; */
    margin: 0 0 1vh 0;
}

.searchbox {
    width: 30rem;
    height: 3.8rem;
    padding-left: 2rem;
    font-size: 1.2rem !important;
    background-color: white;
    border: none;
    outline: none;
    border-radius: 1rem 0 0 1rem;
    background-color: rgba(255, 255, 255, 0.3);
    /* border-bottom: 2px solid #DF8E00; */
    color: #313131;
    /* font-size: 18px;
    font-weight: 300; */
    transition: 0.2s ease-in-out;
    opacity:1;

}

.searchbox:focus {
    background-color: rgba(255, 255, 255, 0.6);
}
.searchbox:placeholder {

}

.go{
    /* margin-top: 20px; */
    width: 10rem;
    height:3.8rem;
    border: none;
    outline: none;
    background-color: #DF8E00;
    border-radius: 0 1rem 1rem 0;
    color: white;
}

.devicelocation{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 2rem 0 1rem 0;
    opacity: 1;
}

.locationbutton{
        
    margin: 0 auto;
    width: 12rem;
    height:3rem;
    border: none;
    outline: none;
    background-color: #e7e7e7;
    border-radius: 0.6rem;
    color: black;
    opacity: 1;
    font-weight: 600;
}

.main{
    /* background-color: red; */
    margin: 1rem 0 0rem 0;
    /* padding: 0vh auto 60px auto; */
    /* background-color:red; */
    /* width: 80rem; */
    width: 60%;
    /* height: 60vh; */
    /* display: flex; */
    /* flex-direction: column; */
    flex-wrap: wrap;
    cursor: pointer;
    border-radius: 2rem;
    box-shadow: 0 2.7rem 5.5rem 0 rgba(0, 0, 0, 0.3),
        0 1.7rem 1.7rem 0 rgba(0, 0, 0, 0.15);
    background-position:top center;
    background-size: cover;
    background-repeat: no-repeat;
    
}

.currenttemp{
    display: flex;
    align-items: center;
    justify-content: center;
}

.currenttemp{
    font-size: 9rem; 
    /* font-size: 10rem;  */
}

.weatherinfo{
    display: flex;
    align-items: center;
    justify-content: center;
    /* margin: -2vh 0 1vh 0; */
